$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-primary-darker: darken($color-primary, 20);
$color-secondary: #101d2c;
$color-secondary-light: #213f61;

.datepicker {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  .calendar-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 40%;
    .text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;
      margin: 1rem;
      font-size: 1.5rem;
      color: $color-secondary;
    }
  }
  //selects
  .selects {
    display: flex;
    height: 35rem;
    width: 60%;
  }
  // room picking part
  .roomselect {
    display: flex;
    flex-direction: column;
    width: 60%;

    .roomselect-title {
      margin: 1rem;
      font-size: 3rem;
      color: $color-secondary;
    }
    .roomselect-options {
      display: flex;
      flex-direction: column;
      height: 100%;
      align-content: center;
    }
    .room {
      margin: 1rem;
    }
    .room-price {
      font-size: 1.5rem;
    }
  }
  // cruise options
  .cruiseselect {
    display: flex;
    flex-direction: column;
    width: 40%;

    .cruise-title {
      margin: 1rem;
      font-size: 3rem;
      color: $color-secondary;
    }
    .cruiseselect-options {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-content: center;
    }
    .cruiseselect-option {
      margin: 1rem;
    }
    .cruiseselect-free {
      font-size: 2rem;
      color: $color-secondary;
    }
    .cruiseselect-special {
      font-size: 2rem;
      color: $color-primary-darker;
      margin: 1rem 0;
    }
  }
  .next-button {
    right: 3rem;
    bottom: 3rem;
    position: absolute;
    padding: 1rem;
    height: min-content;
    width: 25rem;
    align-self: flex-end;
    font-family: inherit;
    background-color: $color-secondary;
    color: white;
    font-size: 2rem;
    text-align: center;
    outline: none;
    border-radius: 0.5rem;
    border: none;
    cursor: pointer;
    &:hover {
      background-color: lighten($color-secondary, 4);
    }
    &:active {
      transform: translateY(0.2rem);
    }
    &:disabled {
      background-color: #566575;
      cursor: not-allowed;
    }
  }
  .total {
    left: 3rem;
    bottom: 3rem;
    position: absolute;
    font-size: 2.5rem;
    color: $color-secondary;
  }
}
.not_suported {
  display: none;
  font-size: 3rem;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
///////////////////////////////////////////////////////////////////////

@media (max-width: 650px) {
  .datepicker {
    flex-direction: column;
    padding: 1rem;
    justify-content: space-around;

    .calendar-container {
      margin-top: 4rem;
      width: 100%;
      height: min-content;
      .text {
        margin: 0.5rem;
      }
    }
    .selects {
      flex-direction: column;
      width: 100%;
    }
    .roomselect {
      flex-direction: column;
      width: 100%;

      .roomselect-title {
        margin: 0.5rem;
        font-size: 2.2rem;
      }
      .roomselect-options {
        flex-direction: row;
        width: 100%;
      }
      .room {
        margin: 0.5rem;
      }
    }
    .cruiseselect {
      width: 100%;
      flex-direction: column;
      .cruise-title {
        margin: 0.5rem;
        font-size: 2.2rem;
      }
      .cruiseselect-options {
        width: 100%;
        flex-direction: row;
      }
      .cruiseselect-free {
        display: flex;
        align-items: center;
        font-size: 1.2rem;
        color: $color-secondary;
      }
      .cruiseselect-special {
        display: flex;
        align-items: center;
        font-size: 1.2rem;
        color: $color-primary-darker;
        margin: 0.5rem 0;
      }
    }
  }
}

@media (max-height: 450px) {
  .datepicker {
    display: none;
  }
  .not_suported {
    display: flex;
  }
}
// css transition animation
.swap-enter {
  transform: translateX(100%);
}
.swap-enter-active {
  transform: translateX(0%);
  transition: transform 900ms ease-in-out;
}

.swap-exit {
  transform: translateX(0%); //0
}
.swap-exit-active {
  transform: translateX(-100%); //100
  transition: transform 900ms ease-in-out;
}
.swap-exit-done {
  display: none;
}
